import React from 'react';
import './App.css';
import Home from "./Home"
import Bio from "./Bio"
import Projects from "./Projects"
import {HashRouter, Route, Switch} from 'react-router-dom'
import 'fontsource-roboto';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Link from '@material-ui/core/Link';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));


function App() {

  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    var id = event.currentTarget.id;

    switch(id) {
      case "menuButton":setAnchorEl(event.currentTarget); break;
      default: break;
    }
    
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
        <div className={classes.root}>
          <AppBar position="static">
            <Toolbar>
              <Typography variant="h6" className={classes.title}>
                Ian Bonafede
              </Typography>
              <Button color="inherit" aria-controls="simple-menu" aria-haspopup="true" id="menuButton" onClick={handleClick}>
                <MenuIcon />
              </Button>
              <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <div><Button href="/ianbonafede/#/" ><HomeIcon fontSize="large"/></Button></div>
                <div><Button href="/ianbonafede/#/bio"><AccountCircleIcon fontSize="large"/></Button></div>
                <div><Button href="/ianbonafede/#/projects"><LibraryBooksIcon fontSize="large"/></Button></div>
                
                
              </Menu>
            </Toolbar>
          </AppBar>
        </div>

        <HashRouter basename={`${process.env.PUBLIC_URL}`}>
          <Switch>

          
            <Route exact path="/" component={Home}/>
            <Route exact path="/bio" component={Bio}/>
            <Route exact path="/Projects" component={Projects}/>
            
          </Switch>
        </HashRouter>
    </>

);

  /*return (
      <>
          <div className={classes.root}>
            <AppBar position="static">
              <Toolbar>
                <Typography variant="h6" className={classes.title}>
                  Ian Bonafede
                </Typography>
                <Button color="inherit" href="/ianbonafede/#/">HOME</Button>
                <Button color="inherit" href="/ianbonafede/#/bio">BIO</Button>
                <Button color="inherit" href="/ianbonafede/#/projects">PROJECTS</Button>
              </Toolbar>
            </AppBar>
          </div>

          <HashRouter basename={`${process.env.PUBLIC_URL}`}>
            <Switch>

            
              <Route exact path="/" component={Home}/>
              <Route exact path="/bio" component={Bio}/>
              <Route exact path="/Projects" component={Projects}/>
              
            </Switch>
          </HashRouter>
      </>

  );*/
  


}



export default App;
